.ner {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.ner-cont {
  position: relative;
  display: flex;
  justify-content: center;
  /* align-items: center; */
  background: #202020cc;
  width: 100%;
  max-width: 1000px;
  min-height: 400px;
  margin: 5px;
  padding: 10px;
  border-radius: 3px;
  -webkit-box-shadow: 0px 2px 14px #0b090a;
  box-shadow: 0px 2px 14px #0b090a;
}
